<template>
  <el-form
    :rules="rules"
    :model="loginForm"
    class="login-container"
    label-position="left"
    label-width="0px"
    v-loading="loading"
  >
    <h3 class="login_title">系统登录</h3>
    <el-form-item prop="username">
      <el-input
        type="text"
        v-model="loginForm.username"
        auto-complete="off"
        placeholder="账号"
        clearable
      ></el-input>
    </el-form-item>
    <el-form-item prop="password">
      <el-input
        type="password"
        v-model="loginForm.password"
        placeholder="密码"
        clearable
        show-password
      ></el-input>
    </el-form-item>
    <el-checkbox class="login_remember" v-model="checked" label-position="left"
      >记住密码</el-checkbox
    >
    <el-form-item style="width: 100%">
      <el-button type="primary" @click.prevent="submitClick" style="width: 100%"
        >登录</el-button
      >
    </el-form-item>
  </el-form>
</template>
<script>
import { doLogin } from "@/api/login";
export default {
  data() {
    return {
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          {
            pattern: /^[a-zA-Z][a-zA-Z0-9_]{4,15}$/,
            message: "字母开头，允许5-16字节，允许字母数字下划线",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            pattern: /^[a-zA-Z]\w{5,17}$/,
            message: "以字母开头，长度在6~18之间，只能包含字母、数字和下划线",
            trigger: "blur",
          },
        ],
      },
      checked: true,
      loginForm: {
        username: "test",
        password: "123456",
      },
      loading: false,
    };
  },
  created() {
    var _this = this;
    // 在页面加载时从cookie获取登录信息
    let userName = _this.getCookie("userName");
    //console.log (userName);
    let userPwd = _this.getCookie("userPwd");
    //console.log (userPwd);

    // 如果存在赋值给表单，并且将记住密码勾选
    if (userName) {
      _this.loginForm.username = userName;
      _this.loginForm.password = userPwd;
      _this.checked = true;
    }
  },
  methods: {
    getCookie: function (key) {
      if (document.cookie.length > 0) {
        var start = document.cookie.indexOf(key + "=");
        if (start !== -1) {
          start = start + key.length + 1;
          var end = document.cookie.indexOf(";", start);
          if (end === -1) end = document.cookie.length;
          return unescape(document.cookie.substring(start, end));
        }
      }
      return "";
    },
    // 保存cookie
    setCookie(cName, value, expiredays) {
      var exdate = new Date();
      exdate.setDate(exdate.getDate() + expiredays);
      document.cookie =
        cName +
        "=" +
        decodeURIComponent(value) +
        (expiredays == null ? "" : ";expires=" + exdate.toGMTString());
    },
    // submitClick: function () {
    //   var _this = this;
    //   this.loading = true;
    //   if (
    //     _this.loginForm.username == "test" &&
    //     _this.loginForm.password == "123456"
    //   ) {
    //     _this.$router.replace({ path: "/layout" });
    //   }
    //   this.$root.isAdmin = true;
    // },
    submitClick() {
      doLogin(this.loginForm.username, this.loginForm.password).then((res) => {
        console.log(res);
        console.log('res.data',res.data);
        let data = JSON.stringify('res.data')
        console.log(data)
        console.log(data.key)
        console.log(data.value)
        if (res.status === 200) {
          console.log("登录成功");
          // 存储token到浏览器
          localStorage.setItem(data.key, data.value);
          this.$router.replace({ path: "/layout" });
        } else {
          // Message.error(res.data.resultMsg);
          console.log("错误");
        }
      });
    },
  },
};
</script>
<style>
.login-container {
  border-radius: 15px;
  background-clip: padding-box;
  margin: 180px auto;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}

.login_title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}

.login_remember {
  margin: 0px 0px 35px 0px;
  text-align: left;
}
</style>
